import { _decorator, Component, Node, SystemEvent, input, Input, EventTouch } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SelfPlane')
export class SelfPlane extends Component {
    start() {
      input.on(Input.EventType.TOUCH_START,this._touchStart,this)
      input.on(Input.EventType.TOUCH_START,this._touchMove,this)
    }
    _touchStart(touch:Touch,event:EventTouch){
      const delta = touch.clientX
      console.log("开始",touch,delta)
    }
    _touchMove(touch:Touch,event:EventTouch){
      console.log("移动")
    }
    update(deltaTime: number) {
        
    }
}

